<template>
  <div class="searchCont" :style="winheight">
    <div class="searchTop">
      <!--   搜索框   -->
      <div class="search">
        <div class="searchInput">
          <span class="iconfont buy-icon-sousuo"></span>
          <input
            type="text"
            class="searchText"
            v-model="value"
            placeholder="法式亚麻T恤"
            v-on:keyup.13="getLable()"
          />
          <span
            class="iconfont buy-icon-qingchu"
            v-show="value"
            @click="value = ''"
          ></span>
        </div>
        <router-link tag="span" to="/" class="cancel">取消</router-link>
      </div>
      <!--   历史信息   -->
      <div></div>
    </div>
    <div class="searchbottom" v-show="!routerQuery">
      <!--   热门   -->
      <div class="hotSearch">热门搜索</div>
      <!--   热门标签   -->
      <div class="recommendLable">
        <span
          v-for="(lable, index) of recommend"
          :key="index"
          class="lable"
          @click="getLable(lable)"
          >{{ lable }}</span
        >
      </div>
    </div>
    <div
      class="searchGoods"
      v-show="searchGoodsList.length === 0 && routerQuery"
    >
      <!--   无搜索内容   -->
      <div class="noGoods">
        <img
          class="noGoodsImg"
          src="http://yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/noSearchResult-309a80c4ec.png"
          alt=""
        />
        <div class="noGoodsTxt">
          您寻找的商品还未上架
        </div>
      </div>
    </div>
    <div class="searchGoods">
      <div></div>
    </div>
  </div>
</template>

<script>
import { search } from "../lib/goods";

export default {
  name: "search",
  data() {
    return {
      recommend: [
        "女鞋",
        "凉席",
        "耳机",
        "杯子",
        "小龙虾",
        "凉鞋",
        "防晒",
        "按摩电器"
      ],
      winheight: {
        height: ""
      },
      value: this.$route.query.searchkey ? this.$route.query.searchkey : "",
      searchGoodsList: [],
      routerQuery: this.$route.query.searchkey
    };
  },
  mounted() {
    this.getHeight();
    console.log(this.routerQuery);
  },
  methods: {
    getHeight() {
      this.winheight.height = document.body.clientHeight + "px";
    },
    getLable(lable) {
      if (!lable && this.value) {
        //回车搜索
        this.$router.push({
          name: "search",
          query: {
            searchkey: this.value
          }
        });
        this.searchGoodsList = search.get_shearch_goods(this.value);
      } else if (lable) {
        //点击标签搜索
        console.log(123456);
        this.$router.push({
          name: "search",
          query: {
            searchkey: lable
          }
        });
        // this.searchGoodsList = search.get_shearch_goods(lable);
      }
    }
  },
  watch: {
    //监听相同路由下参数变化的时候，从而实现异步刷新
    $route(to) {
      console.log(to);
      this.value = to.query.searchkey;
      this.searchGoodsList = [];
      this.routerQuery = to.query.searchkey;
    }
  }
};
</script>

